.profile__name {
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  margin: 0;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  max-width: 375px;
}

@media screen and (max-width: 618px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
    max-width: 240px;
  }
}